<template>
  <tas-base-crud :config="config" hide-detail>
  </tas-base-crud>
</template>

<script>
export default {
  name: 'crud-payroll-periodes-detail',
  data () {
    return {
      config: {
        title: 'Tenaga Kerja Periode',
        model_api: null,
        getter: 'employee-on-periodes',
        setter: 'employee-on-periodes',
        pk_field: 'rel_employee_id',
        permission: {
          create: 'create-payroll-periodes',
          read: 'view-payroll-periodes',
          update: 'update-payroll-periodes',
          delete: 'delete-payroll-periodes'
        },
        slave: [],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          {
            id: 'employee_id',
            label: 'Nama Tenaga Kerja',
            methods: {
              list: { view_data: 'rel_employee_id' },
              create: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              },
              update: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              },
              filter: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              }
            }
          },
          {
            id: 'job_position_id',
            label: 'Jabatan',
            methods: {
              list: { view_data: 'rel_job_position_id' },
              create: {
                setter: 'job-positions',
                getter: 'job-positions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'job_position_name', code: 'id', display: ['job_position_name']
                  }
                }
              },
              update: {
                setter: 'job-positions',
                getter: 'job-positions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'job_position_name', code: 'id', display: ['job_position_name']
                  }
                }
              },
              filter: {
                setter: 'job-positions',
                getter: 'job-positions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'job_position_name', code: 'id', display: ['job_position_name']
                  }
                }
              }
            }
          },
          {
            id: 'status_employment_id',
            label: 'Status Ketenagakerjaan',
            methods: {
              list: { view_data: 'rel_status_employment_id' },
              create: {
                setter: 'custom/master-status-employments',
                getter: 'custom/master-status-employments',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  },
                  get_param: [ { 'id': 'job_position_id' } ]
                }
              },
              update: {
                setter: 'custom/master-status-employments',
                getter: 'custom/master-status-employments',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  },
                  get_param: [ { 'id': 'job_position_id' } ]
                }
              },
              filter: {
                setter: 'custom/master-status-employments',
                getter: 'custom/master-status-employments',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  },
                  get_param: [ { 'id': 'job_position_id' } ]
                }
              }
            }
          },
          {
            id: 'is_mpp',
            label: 'MPP',
            methods: {
              list: {
                order: true,
                class: { false: 'badge badge-danger', true: 'badge badge-primary' },
                transform: 'ynBool'
              },
              detail: false,
              create: {
                type: 'radio',
                value: false,
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Ya', code: true }, { label: 'Tidak', code: false }]
                  }
                }
              },
              update: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Ya', code: true }, { label: 'Tidak', code: false }]
                  }
                }
              },
              filter: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Ya', code: true }, { label: 'Tidak', code: false }]
                  }
                }
              }
            }
          },
          {
            id: 'is_plt',
            label: 'PLT',
            methods: {
              list: {
                order: true,
                class: { false: 'badge badge-danger', true: 'badge badge-primary' },
                transform: 'ynBool'
              },
              detail: false,
              create: {
                type: 'radio',
                value: false,
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Ya', code: true }, { label: 'Tidak', code: false }]
                  }
                }
              },
              update: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Ya', code: true }, { label: 'Tidak', code: false }]
                  }
                }
              },
              filter: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Ya', code: true }, { label: 'Tidak', code: false }]
                  }
                }
              }
            }
          }
        ]
      }
    }
  },
  methods: {

  }
}
</script>



